/*Tags*/
.tags {
  list-style: none;
  margin: 0;
  overflow: hidden;
  padding: 0;
}

.tags h2 {
  padding: 5px 0;
  font: 20px/2em 'Hind';
}

.tags li {
  float: left;
}

.tag {
  font: 15px/1.5 'Lato', serif;
  background: #8bc544;
  border-radius: 3px 0 0 3px;
  color: #FFFFFF;
  display: inline-block;
  height: 26px;
  line-height: 26px;
  padding: 0 20px 0 23px;
  position: relative;
  margin: 0 10px 10px 0;
  text-decoration: none;
  -webkit-transition: color 0.2s;
}

.tag-archive-font {
  font-size: 13px;
}

.tag::before {
  background: #fff;
  border-radius: 10px;
  box-shadow: inset 0 1px rgba(0, 0, 0, 0.25);
  content: '';
  height: 6px;
  left: 10px;
  position: absolute;
  width: 6px;
  top: 10px;
}

.tag::after {
  background: #F2F2F2;
  border-left: 10px solid #8bc544;
  border-top: 13px solid transparent;
  content: '';
  position: absolute;
  right: 0;
  top: 0;
}

.tag:hover {
  background-color: #6b9c30;
  color: white;
  text-decoration: none;
}

.tag:hover::after {
  border-left-color: #6b9c30;
}

